/* src/Calculator.css */
.calculator {
    max-width: 400px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.display {
    background-color: #333;
    color: #fff;
    padding: 20px;
    font-size: 2em;
    text-align: right;
    border-radius: 10px;
    margin-bottom: 10px;
}

.button-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 10px;
}

.button {
    padding: 20px;
    font-size: 1.5em;
    border: none;
    border-radius: 5px;
    background-color: #f0f0f0;
    cursor: pointer;
}

.button:hover {
    background-color: #ddd;
}

.error .display {
    background-color: #ffcccc;
}
.fraction-input {
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
}

.fraction-box {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 5px;
}

.fraction-box input {
    width: 60px;
    padding: 10px;
    font-size: 18px;
    text-align: center;
}

.fraction-box span {
    font-size: 12px;
    margin: 0 10px;
}

.fraction-input button {
    margin-right: 5px;
    padding: 10px;
    width: 30%;
    font-size: 16px;
    cursor: pointer;
}
